@import "sanitize.css/sanitize.css";
@import "sanitize.css/forms.css";
@import "sanitize.css/assets.css";
@import "sanitize.css/typography.css";
@import "sanitize.css/reduce-motion.css";
:root {
  --color-black: #000;
  --color-white: #fff;
  --font-base: var(--font-sans);
  --font-sans:
    Montserrat, -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Ubuntu",
    "Droid Sans", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol";
  --font-serif: Georgia, "Times New Roman", Times, serif;
  --font-mono: SFMono-Regular, Menlo, Monaco, Consolas, Liberation Mono, "Courier New", monospace;
  --body-color: var(--front-primary);
  --body-bg: var(--back-primary);
  --font-size-root: 100%;
  --font-size-base: 1rem;
  --font-weight-base: 400;
  --line-height-base: 1.25;
  --brand-primary: #93c10e;
  --front-primary: #333;
  --front-secondary: #555;
  --front-tertiary: #c8c8c8;
  --front-invert: #fff;
  --back-primary: #fff;
  --back-secondary: #f0f0f0;
  --back-tertiary: #e0e0e0;
  --border-primary: #aaa;
  --link-color: #0069b4;
  --accent-yellow: #ffc709;
  --shadow-default: 0 0 10px rgb(0 0 0 / 10%);
}

:root {
  font-size: var(--font-size-root);
}

html,
body {
  height: 100%;
}

html {
  min-width: 320px;
  min-height: 100%;
  font-size: var(--font-size-root);
  font-family: sans-serif;
  line-height: normal;
  -webkit-tap-highlight-color: rgb(var(--color-black) 0);
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
  color: var(--body-color);
  font-weight: var(--font-weight-base);
  font-size: var(--font-size-base);
  font-family: var(--font-base);
  line-height: var(--line-height-base);
  background-color: var(--body-bg);
}

[tabindex="-1"]:focus:not(:focus-visible) {
  outline: 0;
}

[hidden] {
  display: none;
}

@font-face {
  font-weight: 300;
  font-family: Montserrat;
  font-style: normal;
  font-display: swap;
  src: url("/fonts/Montserrat-Light.woff2") format("woff2");
}
@font-face {
  font-weight: 400;
  font-family: Montserrat;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Montserrat-Regular.woff2") format("woff2");
}
@font-face {
  font-weight: 500;
  font-family: Montserrat;
  font-style: normal;
  font-display: swap;
  src: url("/fonts/Montserrat-Medium.woff2") format("woff2");
}
@font-face {
  font-weight: 600;
  font-family: Montserrat;
  font-style: normal;
  font-display: swap;
  src: url("/fonts/Montserrat-SemiBold.woff2") format("woff2");
}
@font-face {
  font-weight: 700;
  font-family: Montserrat;
  font-style: normal;
  font-display: swap;
  src: url("/fonts/Montserrat-Bold.woff2") format("woff2");
}
.header {
  padding: 8px 0;
  background: var(--back-primary);
  box-shadow: var(--shadow-default);
}
.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 4px 0 9px;
}
.header__left {
  display: flex;
  gap: calc(12px + ( 24 - 12 ) * ( (100vw - 360px) / ( 576 - 360) ));
}
.header__right {
  display: flex;
  gap: calc(0px + ( 8 - 0 ) * ( (100vw - 360px) / ( 576 - 360) ));
  align-items: center;
}
.header__links {
  display: flex;
  gap: calc(0px + ( 20 - 0 ) * ( (100vw - 360px) / ( 576 - 360) ));
}
.header__logo {
  position: absolute;
  top: 0;
  left: 50%;
  z-index: 2;
  display: flex;
  align-items: end;
  width: 136px;
  height: 136px;
  padding: 0 9px 20px;
  background: var(--back-primary);
  border-radius: 50%;
  box-shadow: var(--shadow-default);
  transform: translate(-50%, -54px);
}
.header__icon {
  width: 24px;
  height: 24px;
}
.header__menu {
  padding: 0;
  background: none;
  border: 0;
  appearance: none;
  position: relative;
  width: 32px;
  height: 32px;
  cursor: pointer;
}
.header__burger {
  cursor: pointer;
  -webkit-tap-highlight-color: transparent;
  transition: transform 400ms;
  user-select: none;
}
[data-state=open] .header__burger {
  transform: rotate(45deg);
}
.header__burger-line {
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
  fill: none;
  stroke: var(--brand-primary);
  stroke-width: 3;
  stroke-linecap: round;
}
[data-state=open] .header__burger-line {
  stroke: var(--front-primary);
}
.header__burger-line--top {
  stroke-dasharray: 21 40;
}
.header__burger-line--middle {
  stroke-dasharray: 40 142;
  transform-origin: 50%;
  transition: transform 400ms;
}
.header__burger-line--bottom {
  stroke-dasharray: 17 35;
  transform-origin: 50%;
  transition: transform 400ms, stroke-dashoffset 400ms;
}
[data-state=open] .header__burger-line--top {
  stroke-dashoffset: -32px;
}
[data-state=open] .header__burger-line--middle {
  transform: rotate(90deg);
}
[data-state=open] .header__burger-line--bottom {
  stroke-dashoffset: -83px;
}
.header__discount {
  padding: 0;
  background: none;
  border: 0;
  appearance: none;
  width: 32px;
  height: 32px;
  background: var(--accent-yellow);
  border-radius: 5px;
}
.header__discount .header__icon {
  fill: var(--front-primary);
}
.header__link .header__icon {
  fill: var(--front-tertiary);
}
.header__cart {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 44px;
  height: 44px;
  border-radius: 5px;
}
.header__cart .header__icon {
  fill: var(--brand-primary);
}

.logo {
  width: 100%;
  aspect-ratio: 118/71;
}

.page {
  display: flex;
  flex-direction: column;
  height: 100vh;
}
.page__header {
  position: fixed;
  flex: 0 0 auto;
  width: 100%;
}
.page__main {
  flex: 1 0 auto;
  padding-top: 82px;
}
.page__footer {
  flex: 0 0 auto;
}

